import React,{Component} from 'react';
import {connect} from 'react-redux';
import {CSSTransition} from 'react-transition-group';
import {actionCreators} from './store';
import {actionCreators as loginActionCreators} from '../../pages/login/store';
import {HeaderWrapper,Logo,Nav,NavItem,NavSearch,Addition,Button,SearchWrapper,SearchInfo,SearchInfoTitle,SearchInfoSwitch,SearchInfoList,SearchInfoItem} from './style';
import {Link} from 'react-router-dom';

class Header extends Component{
	render(){
		const { focused,list,login,handleInputFocus,handleInputBlur,logout } = this.props;
		return (
		<HeaderWrapper>
			<Link to="/">
				<Logo /> {/*href*/}
			</Link>
			<Nav>
				<NavItem className="left active"></NavItem>
				<NavItem className="left">App</NavItem>
				{
					login?
					<NavItem className="right" onClick={logout}></NavItem>:
					<Link to="/login"><NavItem className="right"></NavItem></Link>
				}
				<NavItem className="right">
					<i className="iconfont">&#xe636;</i>
				</NavItem>
				<SearchWrapper>
					{/*classNames*/}
					<CSSTransition
						in={focused}
						timeout={200}
						classNames="slide"
						>
					<NavSearch
						className={focused?'focused':''}
						onFocus={()=>{handleInputFocus(list)}}
						onBlur={handleInputBlur}></NavSearch>
					</CSSTransition>
					<i className={focused?'focused iconfont zoom':'iconfont zoom'}>&#xe617;</i>
					{this.getListArea()}
				</SearchWrapper>
			</Nav>
			<Addition>
				<Link to='/write'><Button className="writing"><i className="iconfont">&#xe615;</i></Button></Link>
				<Button className="reg"></Button>
			</Addition>
		</HeaderWrapper>
		)
	}

	getListArea(){
		const { focused,list,page,totalPage,mouseIn,handleMouseEnter,handleMouseLeave,handleChangePage } = this.props;
		const newList = list.toJS();//listimmutablejs
		const pageList = [];
		if(newList.length){//ajaxnewlistkeykey
			for(let i=(page-1)*10;i<page*10;i++){
				pageList.push(
					<SearchInfoItem key={newList[i]}>{newList[i]}</SearchInfoItem>
				)
			}
		}
		if(focused||mouseIn){
			return(
				<SearchInfo onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
					<SearchInfoTitle>
						
						<SearchInfoSwitch onClick={()=>{handleChangePage(page,totalPage,this.spinIcon)}}>
							<i ref={(icon)=>{this.spinIcon=icon}} className='iconfont spin'>&#xe851;</i>
							
						</SearchInfoSwitch>
					</SearchInfoTitle>
					<SearchInfoList>
						{ pageList }
					</SearchInfoList>
				</SearchInfo>
			);
		}else{
			return null;
		}
	}
}

const mapStateToProps = (state)=>{
	return{
		//header:reducerstateimmutablestate.get('header')immutableget
		//focused : state.get('header').get('focused')
		//immutablegetIn
		focused:state.getIn(['header','focused']),
		list:state.getIn(['header','list']),
		page:state.getIn(['header','page']),
		mouseIn:state.getIn(['header','mouseIn']),
		totalPage:state.getIn(['header','totalPage']),
		login:state.getIn(['login','login'])
	}
}

const mapDispatchToProps = (dispatch)=>{
	return{
		handleInputFocus(list){
			(list.size === 0 ) && dispatch(actionCreators.getList());//ajax
			dispatch(actionCreators.searchFocus())
		},
		handleInputBlur(){
			dispatch(actionCreators.searchBlur())
		},
		handleMouseEnter(){
			dispatch(actionCreators.mouseEnter())
		},
		handleMouseLeave(){
			dispatch(actionCreators.mouseLeave())
		},
		handleChangePage(page,totalPage,spin){
			let originAngle = spin.style.transform.replace(/[^0-9]/ig,'');//spinstyletransform,
			if(originAngle){
				originAngle = parseInt(originAngle,10);//originAngle10
			}else{
				originAngle = 0;//originAngle
			}
			spin.style.transform = 'rotate('+(originAngle+360)+'deg)';
			if(page<totalPage){
				dispatch(actionCreators.changePage(page+1))
			}else{
				dispatch(actionCreators.changePage(1))
			}
		},
		logout(){
			dispatch(loginActionCreators.logout())
		}
	}
}
export default connect(mapStateToProps,mapDispatchToProps)(Header);


