import { combineReducers } from 'redux-immutable'; //redux-immutablecombineReducersreducerreducer
import {reducer as headerReducer} from '../common/header/store';//index.js
import {reducer as homeReducer} from '../pages/home/store';
import {reducer as detailReducer} from '../pages/detail/store';
import {reducer as loginReducer} from '../pages/login/store';
//
//redux-immutablecombineReducersimmutable
const reducer = combineReducers({
	header: headerReducer,/*state.header.*/
	home: homeReducer,
	detail:detailReducer,
	login:loginReducer
});

export default reducer;



